.token-list {
    display: flex;
    flex-direction: column;
    background: black;
    border-radius: 8px;
    background: #1a1a1a;
    width: 100%;
  }
  
  .token-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    padding: 15px;
    width: 100%;
  }
  
  .token-info {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .token-logo {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  
  .token-label {
    font-size: 16px;
    font-weight: light;
    color: white;
    margin: 0;
  }
  
  .token-variation {
    font-size: 14px;
    margin: 0;
    text-align: left;
    font-size: 12px;
    margin-top: 4px;
  }
  
  .positive {
    color: #33cc33;
  }
  
  .negative {
    color: #ff3333;
  }
  
  .token-values {
    text-align: right;
  }
  
  .token-amount {
    font-size: 16px;
    font-weight: bold;
    color: white;
    margin: 0;
  }
  
  .token-price {
    font-size: 14px;
    color: gray;
    margin: 0;
  }

  .detail-row{
    display: flex;
    justify-content: space-between;
    text-align: right;
    padding: 12px 16px;
    border-bottom: 1px solid #3f3f40;
    max-width: 200px;
  }

  .last-row{
    border: none;
  }

  .token-data {
    display: flex;
    flex-direction: column;
    
    background: black;
    border-radius: 12px;
    background: #1a1a1a;
    width: 100%;
    font-size: 14px;
    
  }